.bordered{font-family:Didot,serif;font-size:2.5rem;font-style:italic;line-height:1}@media only screen and (min-width:1024px){.bordered{font-size:3.25vw}}@media only screen and (min-width:1440px){.bordered{font-size:2.75vw}}.preface{font-family:Gotham Book,sans-serif;font-size:.75rem;letter-spacing:.16667em;line-height:1;text-transform:uppercase}.cover{-webkit-transform:translate(-50%,-50%);position:absolute;transform:translate(-50%,-50%);transition:opacity .4s}.cover,.wrapper{height:auto;left:50%;min-height:100%;min-width:100%;top:50%;width:auto}.wrapper{-webkit-transform:translateX(-50%) translateY(-50%);background:#000;background-size:cover;position:fixed;transform:translateX(-50%) translateY(-50%);z-index:-1}.heading{bottom:15%;left:0;position:absolute;top:15%;width:100%;z-index:1}@media only screen and (min-width:768px){.heading{-webkit-transform:translateY(-50%);bottom:auto;top:50%;transform:translateY(-50%)}}.preface{color:hsla(0,0%,100%,.6);line-height:24px;text-align:center}.center{align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;flex-flow:column;height:100%;justify-content:space-evenly;margin:0 auto;position:relative}@media only screen and (min-width:768px){.center{flex-flow:row;height:auto}}.bordered{border:1px solid hsla(0,0%,100%,.15)!important;padding:25px;transition-delay:.2s;transition-duration:.2s;transition-property:border;transition-timing-function:ease-out}@media only screen and (min-width:768px){.bordered{padding:50px}}.bordered:hover{border:1px solid #fff!important}


