.align{align-items:center;flex-wrap:nowrap;height:100%;justify-content:flex-start}.scroller{height:100%;left:0;position:fixed;top:0;width:100%;z-index:1}.list{font-size:1.3rem;z-index:1}.list,.wrapper{composes:align;display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;position:relative}.wrapper{padding-left:24px;padding-right:24px}@media only screen and (min-width:768px){.wrapper:first-of-type{padding-left:56px}}.inner{align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;position:relative;width:calc(100vw - 48px)}.progress{-webkit-transform:scaleX(0);-webkit-transform-origin:0 0;background:#ffc332;display:block;height:1px;left:0;position:absolute;top:50%;transform:scaleX(0);transform-origin:0 0;width:100%;will-change:transform;z-index:0}


