.slider{color:#fff;overflow:hidden}.slider,.slides{height:100%;position:absolute;width:100%}.nav{bottom:24px;display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;justify-content:space-between;left:24px;min-width:85px;position:absolute;z-index:5}@media only screen and (min-width:768px){.nav{min-width:126px}}@media only screen and (min-width:1024px){.nav{bottom:48px;left:48px}}.nav div:first-child svg{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.slide{align-content:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;flex-direction:column;height:100%;justify-content:center;overflow:hidden;pointer-events:none;width:100%}.counter,.slide{position:absolute}.counter{display:none}


