.cursor{opacity:1;visibility:inherit}.pointer{-webkit-backface-visibility:hidden;-webkit-transform-style:preserve-3d;backface-visibility:hidden;border-radius:50%;cursor:none;left:50%;opacity:1;pointer-events:none;position:absolute;top:50%;transform-style:preserve-3d;will-change:transform;z-index:1000}.outer{background:transparent;border:1px solid #d7792c;composes:pointer;height:40px;margin:-20px 0 0 -20px;transition:opacity .3s,transform .3s;width:40px}.outer.isHover,.outer.isMouseDown{-webkit-transform:scale(.8);transform:scale(.8)}.outer.isHover{opacity:0}.outer.isMin{-webkit-transform:scale(0);transform:scale(0)}.inner{background:#d7792c;composes:pointer;height:8px;margin:-4px 0 0 -4px;transition:opacity .3s,transform .3s;width:8px}.inner.isMouseDown{-webkit-transform:scale(1.25);transform:scale(1.25)}.inner.isHover{opacity:0}.inner.isMin{-webkit-transform:scale(1.5);transform:scale(1.5)}.inner.isLoading{-webkit-animation-duration:1s;-webkit-animation-iteration-count:infinite;-webkit-animation-name:beating;animation-duration:1s;animation-iteration-count:infinite;animation-name:beating}@-webkit-keyframes beating{0%{-webkit-transform:scale(1);transform:scale(1)}70%{-webkit-transform:scale(2.25);transform:scale(2.25)}}@keyframes beating{0%{-webkit-transform:scale(1);transform:scale(1)}70%{-webkit-transform:scale(2.25);transform:scale(2.25)}}


