.inner{display:inline-block;height:18px;pointer-events:none;position:relative;width:18px}@media only screen and (min-width:768px){.inner{height:24px;width:24px}}.inner span{background:#161516;display:block;height:1px;left:0;position:absolute;top:50%;transition:.25s;width:100%}.inner span:first-of-type{-webkit-transform:translateY(5px);transform:translateY(5px)}.inner span:nth-of-type(3){-webkit-transform:translateY(-5px);transform:translateY(-5px)}.inner.isClose span:first-of-type{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.inner.isClose span:nth-of-type(2){-webkit-transform:scaleX(0);-webkit-transform-origin:100% 100%;transform:scaleX(0);transform-origin:100% 100%}.inner.isClose span:nth-of-type(3){-webkit-transform:rotate(45deg);transform:rotate(45deg)}.inner.inverted span{background:#fff}.inner:hover span{background:#fff}


